<template>
  <div>
    <div v-for="(item,index) in paperDetailVO" :key="index">
      <el-row class="issue" v-if="item.issueTypeID == 1">
        <el-col>
          <div>
            <span>第{{item.issueNum}}题：{{item.issueContent}}</span>
          </div>
          <div>
            <el-radio-group v-model="radioPick[item.issueNum]" @change="radioChange">
              <el-radio label="A">A.{{item.optionA}}</el-radio>
              <el-radio label="B">B.{{item.optionB}}</el-radio>
              <el-radio label="C">C.{{item.optionC}}</el-radio>
              <el-radio label="D">D.{{item.optionD}}</el-radio>
            </el-radio-group>
          </div>
        </el-col>
      </el-row>
      <el-row class="issue" v-else-if="item.issueTypeID == 2">
        <el-col>
          <div>
            <span>第{{item.issueNum}}题：{{item.issueContent}}</span>
          </div>
          <div>
            <!-- <input type="checkbox" v-model="checkboxPick[item.issueNum]" value="A" />{{item.optionA}}
            <input type="checkbox" v-model="checkboxPick[item.issueNum]" value="B" />{{item.optionB}}
            <input type="checkbox" v-model="checkboxPick[item.issueNum]" value="C" />{{item.optionC}}
            <input type="checkbox" v-model="checkboxPick[item.issueNum]" value="D" />{{item.optionD}}-->
            <el-checkbox-group v-model="checkboxPick[item.issueNum]" @change="checkboxChange">
              <el-checkbox label="A">A.{{item.optionA}}</el-checkbox>
              <el-checkbox label="B">B.{{item.optionB}}</el-checkbox>
              <el-checkbox label="C">C.{{item.optionC}}</el-checkbox>
              <el-checkbox label="D">D.{{item.optionD}}</el-checkbox>
            </el-checkbox-group>
          </div>
        </el-col>
      </el-row>
      <el-row class="issue" v-else>
        <el-col>
          <div>
            <span>第{{item.issueNum}}题：{{item.issueContent}}</span>
          </div>
          <div>
            <el-radio-group v-model="radioPick[item.issueNum]" @change="radioChange">
              <el-radio label="Y">正确</el-radio>
              <el-radio label="N">错误</el-radio>
            </el-radio-group>
          </div>
        </el-col>
      </el-row>
    </div>
    <div>
      <el-row>
        <el-col>
          <el-button size="small" @click="post" type="primary">
            <i class="el-icon-circle-plus-outline" />提交问卷
          </el-button>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
import Qs from "qs";

export default {
  data() {
    return {
      paperDetailVO: [],
      radioPick: [],
      checkboxPick: [
        [],
        [],
        [],
        [],
        [],
        [],
        [],
        [],
        [],
        [],
        [],
        [],
        [],
        [],
        [],
        [],
        [],
        [],
        [],
        [],
        [],
        [],
        [],
        [],
        [],
        [],
        [],
        [],
        [],
        [],
        [],
        [],
        [],
        []
      ],
      answer: {
        paperID: this.$route.params.paperID,
        userID: sessionStorage.getItem("userID"),
        finalPoint: 0
      },
      feedbacks: []
    };
  },
  methods: {
    radioChange() {
      console.log("单选pick为：" + JSON.stringify(this.radioPick));
    },
    checkboxChange() {
      console.log("多选pick为：" + JSON.stringify(this.checkboxPick));
    },
    post() {
      //生成反馈表返回集合
      for (var i = 1; i <= this.paperDetailVO.length; i++) {
        var feedback = {
          userID: sessionStorage.getItem("userID"),
          paperID: this.$route.params.paperID,
          issueNum: 0,
          userOption: ""
        };

        if (
          this.paperDetailVO[i - 1].issueTypeID == 1 ||
          this.paperDetailVO[i - 1].issueTypeID == 3
        ) {
          feedback.issueNum = i;
          feedback.userOption = this.radioPick[i];
          this.feedbacks[i - 1] = feedback;
        } else {
          feedback.issueNum = i;
          var checkboxPick = this.checkboxPick[i];
          var answer = "";
          for (var j = 0; j < checkboxPick.length; j++) {
            //拼接字符串'A'、'B'、'C'、'D'
            answer = answer + checkboxPick[j];
          }
          feedback.userOption = answer;
          this.feedbacks[i - 1] = feedback;
        }
      }
      console.log("反馈集合生成啦！");
      //计算答题得分
      var finalPoint = 0;
      for (var i = 1; i <= this.paperDetailVO.length; i++) {
        if (
          this.paperDetailVO[i - 1].issueTypeID == 1 ||
          this.paperDetailVO[i - 1].issueTypeID == 3
        ) {
          if (this.paperDetailVO[i - 1].answer == this.radioPick[i]) {
            //答案正确
            finalPoint = finalPoint + this.paperDetailVO[i - 1].point;
          } else {
            //答案错误
            finalPoint = finalPoint + 0;
          }
        } else {
          var checkboxPick = this.checkboxPick[i].sort();
          var answer = "";
          for (var j = 0; j < checkboxPick.length; j++) {
            //拼接字符串'A'、'B'、'C'、'D'
            answer = answer + checkboxPick[j];
          }
          if (this.paperDetailVO[i - 1].answer == answer) {
            //答案正确
            finalPoint = finalPoint + this.paperDetailVO[i - 1].point;
          } else {
            //答案错误
            finalPoint = finalPoint + 0;
          }
        }
      }
      this.answer.finalPoint = finalPoint;

      let data = {
        feedbacks: this.feedbacks,
        userID: sessionStorage.getItem("userID"),
        paperID: this.$route.params.paperID
      };
      console.log("data为" + JSON.stringify(data));
      var api1 = "http://localhost:8888/paper/add/feedback";
      this.axios //提交反馈表
        .post(api1, data)
        .then(response => {
          console.log("反馈表response" + response);
        })
        .catch(error => {
          console.log(error);
        });

      var api2 = "http://localhost:8888/paper/add/answer";
      this.axios //提交答卷表
        .post(api2, Qs.stringify(this.answer))
        .then(response => {
          console.log("答卷表response" + response);
          if (response.data == 200) {
            this.$message({
              showClose: true,
              message: "提交成功！",
              type: "success"
            });
          }
          this.$router.push({
            name: "FinalPoint",
            params: { current: 1, size: 5 }
          });
        })
        .catch(error => {
          console.log(error);
          this.$message({
            showClose: true,
            message: "提交失败，请联系维护人员",
            type: "error"
          });
        });
    },
    getData() {
      var api =
        "http://localhost:8888/paper/detail/" + this.$route.params.paperID;
      this.axios
        .get(api)
        .then(response => {
          console.log(response);
          this.paperDetailVO = response.data;
          // console.log("所有试题为：" + JSON.stringify(this.paperDetailVO))
        })
        .catch(error => {
          console.log(error);
        });
    }
  },
  created() {
    this.getData();
  }
};
</script>

<style lang="scss" scoped>
.issue {
  height: 100px;
  border-radius: 2px;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04);
  font: 16px Medium;
  margin-bottom: 5px;
}
</style>